const Header = (props) => (
    <div className="navBar">
        <ul>
            {
                props.data.forEach(item => {
                    console.log(item.label)
                })
            }
            
            {props.data.forEach(item => (
                <li>
                    <a href={item.href} className="aNavbar">{item.label}</a>
                </li>
            ))}
        </ul>
    </div>
)

export default Header